import { createFeatureSelector, createSelector } from '@ngrx/store';
import { IConfigDesignerModel, IConfigDesignerState } from './config-designer.types';
import { RX_CONFIG_DESIGNER } from '../../config-designer.constant';

export const configDesignerStateSelector = createFeatureSelector<IConfigDesignerState>(
  RX_CONFIG_DESIGNER.featureSelector
);

export const configDesignerModelSelector = createSelector(
  configDesignerStateSelector,
  (configDesignerState: IConfigDesignerState) => configDesignerState.model
);

export const bundleIdSelector = createSelector(
  configDesignerModelSelector,
  (configDesignerModel: IConfigDesignerModel) => configDesignerModel.bundleId
);

export const definitionNameSelector = createSelector(
  configDesignerModelSelector,
  (configDesignerModel: IConfigDesignerModel) => configDesignerModel.definitionName
);

export const isDesignModeSelector = createSelector(
  configDesignerModelSelector,
  (configDesignerModel: IConfigDesignerModel) => configDesignerModel.isDesignMode
);

export const inspectorTabIndexSelector = createSelector(
  configDesignerModelSelector,
  (configDesignerModel: IConfigDesignerModel) => configDesignerModel.inspectorTabIndex
);

export const parentComponentsSelector = createSelector(
  configDesignerModelSelector,
  (configDesignerModel: IConfigDesignerModel) => configDesignerModel.parentComponents
);

export const selectedFieldGuidSelector = createSelector(
  configDesignerModelSelector,
  (configDesignerModel: IConfigDesignerModel) => configDesignerModel.selectedFieldGuid
);

export const definitionModelFromDefinitionSelector = createSelector(
  configDesignerModelSelector,
  (configDesignerModel: IConfigDesignerModel) => configDesignerModel.definitionModelFromDefinition
);

export const definitionModelSelector = createSelector(
  configDesignerModelSelector,
  (configDesignerModel: IConfigDesignerModel) => configDesignerModel.definitionModel
);

export const isDirtySelector = createSelector(
  configDesignerModelSelector,
  (configDesignerModel: IConfigDesignerModel) => configDesignerModel.isDirty
);

export const savedDefinitionNameSelector = createSelector(
  configDesignerModelSelector,
  (configDesignerModel: IConfigDesignerModel) => configDesignerModel.savedDefinitionName
);
